import {
  BadGatewayException,
  BadRequestException,
  Injectable,
  InternalServerErrorException,
  NotFoundException,
} from '@nestjs/common';

import { ReportRepository } from '../../../domain/repository';
import { ProjectReportId } from '../../../domain/value';
import { CustomLogger } from '../../../utility';
import {
  ICreateProjectCommonReportRequest,
  IProjectCommonReportResponse,
  IUpdateProjectCommonReportRequest,
} from '../../interface';

import { ProjectCommonReportParser } from './parser';

@Injectable()
export class ProjectCommonReportScenario {
  readonly parser: ProjectCommonReportParser;

  constructor(
    readonly logger: CustomLogger,
    readonly repository: ReportRepository,
  ) {
    this.logger.setContext(ProjectCommonReportScenario.name);
    this.parser = new ProjectCommonReportParser(this.logger);
  }

  async pushProjectCommonReport(
    input: ICreateProjectCommonReportRequest,
  ): Promise<IProjectCommonReportResponse> {
    try {
      const entity = this.parser.convertToProjectReport(input);

      const result = await this.repository.createProjectCommonReport(entity);

      return this.parser.convertProjectReportToReference(result);
    } catch (error) {
      if (
        [BadRequestException, BadGatewayException].includes(error.constructor)
      ) {
        throw error;
      }

      this.logger.error(error);
      throw new InternalServerErrorException(error.message);
    }
  }

  async update(
    id: string,
    updateData: IUpdateProjectCommonReportRequest,
  ): Promise<IProjectCommonReportResponse> {
    try {
      const entity = await this.repository.getProjectCommonReportById(
        ProjectReportId.from(id),
      );

      const data = this.parser.formatDataToUpdate(
        entity.createUserId.value,
        updateData,
      );
      const updatedEntity = entity.update(data);

      const result =
        await this.repository.updateProjectCommonReport(updatedEntity);

      return this.parser.convertProjectReportToReference(result);
    } catch (error) {
      if (
        [BadRequestException, BadGatewayException, NotFoundException].includes(
          error.constructor,
        )
      ) {
        throw error;
      }

      this.logger.error(error);
      throw new InternalServerErrorException(error.message);
    }
  }
}
